
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


export default{
components: {
Cell,
Group
},
data () {
return {
data: {},
isCate: false,
cateList: []
}
},
mounted () {
this.init_data()
},
methods: {
routerLink (id) {
this.$router.push({path: './page/'+id})
},
init_data () {
this.$rest(this.data_uri).get()
.success(data=>{
this.data = data.list
if(this.data[0].is_cate){
this.isCate = true
this.init_cate(this.data.page_id)
}
}).error(response=>{
this.toast(response.body.message||'获取数据失败~','error')
})
},
init_cate (id) {
id = id||1
let uri = 'pages?parent_pid='+id+'&with_count=children_num'
this.$rest(uri).get()
.success(data=>{
this.cateList = data.list
}).error(response=>{
this.toast(response.body.message||'获取数据失败~','error')
})
}
},
computed: {
data_uri () {
let id = this.$route.params.id||1

return 'pages?page_id='+id
}
}
}



